import React from 'react';
import {useSearchContext} from "../context/SearchContext";
import './SearchStyle.css'
import {Link} from "react-router-dom";
const SearchResult = () => {
    const {searchData} = useSearchContext()
    return (
        <div>
            {searchData && searchData.coins && Object.keys(searchData.coins).length > 0 && (
                <div className={'content'}>

                    <h2>Coin Results:</h2>
                    <div className="card-container">
                        {/* Display or process coin data as needed */}
                        {Object.values(searchData.coins).map((coin) => (
                            <Link to={`/coin/${coin.id}`}>
                            <div key={coin.id} className="cardSearch">
                                <h3>{coin.name}</h3>
                                <img className={"search-img"} src={coin.large} alt={`${coin.name} thumbnail`}/>
                            </div>
                            </Link>
                        ))}
                    </div>
                </div>
            )}
            {searchData && searchData.nfts && Object.keys(searchData.nfts).length > 0 && (
                <div className={'content'}>

                    <h2>NFTs Results:</h2>
                    <div className="card-container">
                        {/* Display or process coin data as needed */}
                        {Object.values(searchData.nfts).map((nft) => (
                            <Link to={`/nft/${nft.id}`} >
                            <div key={nft.id} className="nftCard">
                                <h3>{nft.name}</h3>
                                <img className={"nftImg"} src={nft.thumb} alt={`${nft.name} thumbnail`}/>
                            </div>
                        </Link>
                        ))}
                    </div>
                </div>
            )}
            {searchData && searchData.categories && Object.keys(searchData.categories).length > 0 && (
                <div className={'content'}>

                    <h2>Categories Results:</h2>
                    <div className="card-container">
                        {/* Display or process coin data as needed */}
                        {Object.values(searchData.categories).map((coin) => (
                            // console.log(coin?.name?.toLowerCase().replace(/\s+/g, '-'))
                            <Link to={`/categories/${coin?.name?.toLowerCase().replace(/\s+/g, '-')}`} state={{
                                coin: {
                                    name:coin.name,
                                    rank:"-",
                                    coins_count:"-",
                                    data:{
                                        market_cap:"No data",
                                        market_cap_change_percentage_24h:{
                                            usd:"No data"
                                        },
                                        sparkline:""

                                    }
                                },
                            }}>
                            <div key={coin?.id} className="cardSearch">
                                <h3>{coin?.name}</h3>
                                {/*<img className={"search-img"} src={coin.thumb} alt={`${coin.name} thumbnail`}/>*/}
                            </div>
                            </Link>
                        ))}
                    </div>
                </div>
            )}
            {searchData && searchData.exchanges && Object.keys(searchData.exchanges).length > 0 && (
                <div className={'content'}>

                    <h2>exchanges Results:</h2>
                    <div className="card-container">
                        {/* Display or process coin data as needed */}
                        {Object.values(searchData.exchanges).map((exchange) => (
                            <Link to={`/exchanges/${exchange.id}`}>
                            <div key={exchange.id} className="cardSearch">
                                <h3>{exchange.name}</h3>
                                <img className={"search-img"} src={exchange.large} alt={`${exchange.name} thumbnail`}/>
                                {/*<p>{exchange.market_type}</p>*/}
                            </div>
                            </Link>
                        ))}
                    </div>
                </div>
            )}

        </div>
    );
};

export default SearchResult;

