import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import axios from "axios";
import DOMPurify from 'dompurify';
import {CopyToClipboard} from "react-copy-to-clipboard";
import { IoIosCopy, IoIosCheckmarkCircleOutline } from 'react-icons/io'
import InformationHeader from "../components/InformationTemplates/InformationHeader";
import {capitalizeFirstLetter, formatPercentages, nftTableHeaders} from "../utils/utils";
import TimelineTable from "../components/InformationTemplates/TimelineTable";
import StatsTable from "../components/InformationTemplates/StatsTable";
import AboutSection from "../components/InformationTemplates/AboutSection";
import AdressSection from "../components/InformationTemplates/AdressSection";

// https://www.bojanjagetic.com/post/react-syntaxhighlighter-copy-clipboard
const NftInfo = () => {
    const [nftInfo, setNftInfo] = useState({});
    const params = useParams();

    useEffect(() => {
        const url = `https://api.coingecko.com/api/v3/nfts/${params.nftId}`
        axios.get(url).then((res) => {
            console.log(res.data)
            setNftInfo(res.data)
        }).catch((error) => {
            console.log(error)
        })
    }, [params.nftId]);
    const statsTitles1 = {
        hasTitle: false,
        mainTitle:"",
        left_1: "Volume 24h",
        left_2: "Volume 24h % change",
        right_1: "Market Cap",
        right_2: "Market Cap 24h % Change"
    }
    const statsTable1 ={
        left_1: "$"+nftInfo?.volume_24h?.usd?.toLocaleString(),
        left_2: formatPercentages(nftInfo?.volume_24h_percentage_change?.usd),
        right_1: "$"+nftInfo?.market_cap?.usd?.toLocaleString(),
        right_2: formatPercentages(nftInfo?.market_cap_24h_percentage_change?.usd),
    }

    const statsTitles2 = {
        hasTitle: true,
        mainTitle:`Native currency: ${capitalizeFirstLetter(nftInfo?.native_currency || "")} (${nftInfo?.native_currency_symbol})`,
        left_1: "Unique addresses",
        left_2: "24h % change",
        right_1: "Avg sale price",
        right_2: "24h % change"
    }
    const statsTable2 ={
        left_1: nftInfo?.number_of_unique_addresses?.toLocaleString(),
        left_2: formatPercentages(nftInfo?.number_of_unique_addresses_24h_percentage_change),
        right_1:`${nftInfo?.one_day_average_sale_price?.toLocaleString()} ${nftInfo?.native_currency_symbol}`,
        right_2: formatPercentages(nftInfo?.one_day_average_sale_price_24h_percentage_change),
    }
    // <TimelineTable infoObject={nftInfo} tableHeaders={nftTableHeaders}/>
    return (
        <div>
            <div className={'coin-container'}>
                <InformationHeader infoObject={nftInfo}/>
                <TimelineTable infoObject={nftInfo} tableHeaders={nftTableHeaders}/>
                <StatsTable stats={statsTable1} titles={statsTitles1}/>
                <AboutSection description={nftInfo?.description}/>
                <StatsTable stats={statsTable2} titles={statsTitles2}/>

                {/*will add Toast message*/}
                <AdressSection contractAddress={nftInfo?.contract_address}/>

            </div>

            {/*<CoinLineChart />*/}
        </div>
    );
};

export default NftInfo;