import {FaCoins} from "react-icons/fa6";
import './Navbar.css'
import {Link} from "react-router-dom";
import {useState} from "react";
import {Nav, NavDropdown} from "react-bootstrap";
import {useSearchContext} from "../context/SearchContext";
import dodgeIco from '../images/dodge64.png'
import { RxDropdownMenu } from "react-icons/rx";
import bigdodge from '../images/favicon.ico'
// https://www.iconarchive.com/show/dogecoin-to-the-moon-icons-by-iconarchive/Dogecoin-Bitcoin-icon.html



// https://www.studytonight.com/code/playground/web/?id=YbAkzp
// dropdown with css
const NavBar = () => {
    const [showDropdown, setShowDropdown] = useState(false);
    const {handleSearch,searchInput,setSearchInput}=useSearchContext()
    const toggleDropdown = () => {
        setShowDropdown(!showDropdown);
    };
    const handleLinkClick = () => {
        setShowDropdown(false);
    };
    const handleKeyPress = (e) => {
        if (e.key === 'Enter' && searchInput!=='') {
            // Trigger search function when Enter is pressed
            handleLinkClick()
            handleSearch();
        }
    };

    // <div className="navbar-logo">
    //     <h1>
    //         <FaCoins className={'icon'}/>
    //         <div className="dropdown">
    //             Crypto Search
    //             <div className="dropdown-menu">
    //                 <Link to="/">Home</Link>
    //                 <Link to="/globalMarketData">Market Data</Link>
    //                 <Link to="/exchanges">Exchanges</Link>
    //                 <Link to={'/nftlist'}>NFT List</Link>
    //             </div>
    //         </div>
    //     </h1>
    // </div>
    return (
        <div className={'content'}>
            <div className="navbar">


                <div className="navbar-logo">

                    <h1>
                        <div className="dropdown" onClick={toggleDropdown}>
                            <RxDropdownMenu className={'dropdownIcon'}/>
                            <div className="dropdown-menu">
                                <Link to="/" onClick={handleLinkClick}>
                                    Home
                                </Link>
                                <Link to="/globalMarketData" onClick={handleLinkClick}>
                                    Market Data
                                </Link>
                                <Link to="/exchangesList" onClick={handleLinkClick}>
                                    Exchanges
                                </Link>
                                <Link to={'/nftlist'} onClick={handleLinkClick}>
                                    NFT List
                                </Link>
                            </div>
                        </div>

                        {/*<FaCoins className={'icon'}/>*/}
                        <img className={'icon'} src={dodgeIco} alt={''}/>
                            <Link to={'/'}>
                            Crypto Search
                            </Link>

                    </h1>

                    {showDropdown && (
                        <div className="dropdown-mobile">
                            <Link to="/" onClick={handleLinkClick}>
                                Home
                            </Link>
                            <Link to="/globalMarketData" onClick={handleLinkClick}>
                                Market Data
                            </Link>
                            <Link to="/exchangesList" onClick={handleLinkClick}>
                                Exchanges
                            </Link>
                            <Link to={'/nftlist'} onClick={handleLinkClick}>
                                NFT List
                            </Link>
                        </div>
                    )}
                </div>

                <div className="navbar-links">
                    <Link to="/" className="nav-link">
                        Home
                    </Link>
                    <Link to="/globalMarketData" className="nav-link">
                        Market Data
                    </Link>
                    <Link to="/exchangesList" className={'nav-link'}>Exchanges</Link>
                    <Link to={'/nftlist'} className={'nav-link'}>NFT List</Link>
                </div>

                <div className="search-container">
                    <input  type="text" placeholder="Search..." className="dark-input" onKeyDown={handleKeyPress} value={searchInput} onChange={(e)=>{
                        setSearchInput(e.target.value)
                    }}/>
                    <button onClick={(e)=> {
                        e.preventDefault()
                        // setSearchInput(event.target.value)
                        console.log('This is after i press the search button')
                        handleSearch()
                        // console.log(event.target.value)
                    }}>Search</button>
                </div>
            </div>
        </div>
    );
};
export default NavBar

