import React from 'react';
import {capitalizeFirstLetter, formatNumber} from "../../utils/utils";

const InformationHeader = ({infoObject}) => {
    const {name, market_cap_rank, image, symbol, market_data, asset_platform_id, floor_price} = infoObject
    return (
        <div>
            <div className="content">
                <h1>{name} </h1>
            </div>
            <div className="content">
                <div className="rank">
                    {market_cap_rank ?
                        <span className={'rank-btn'}>Rank # {market_cap_rank}</span> : null}
                    {asset_platform_id ?
                        <span className={'rank-btn'}>Platform: {capitalizeFirstLetter(asset_platform_id)}</span> : null}
                </div>
                <div className="info">
                    <div className="coin-heading">
                        {image ? <img src={image.small} alt={''}/> : null}
                        <p>{name}</p>
                        {symbol ? <p>{symbol.toUpperCase()}/USD</p> : null}
                    </div>
                    <div className="coin-price">
                        {market_data ?
                            <h1>${formatNumber(market_data.current_price.usd)}</h1> : null}
                        {floor_price ? <h1>${floor_price.usd.toLocaleString()} </h1> : null}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default InformationHeader;