import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';
import {useEffect, useState} from "react";
import axios from "axios";
import {fetchAPI, formatNumber} from "../../utils/utils";
import StatsTable from "../InformationTemplates/StatsTable";

ChartJS.register(ArcElement, Tooltip, Legend);



const CoinPieChart = () => {
    const [globalData, setGlobalData] = useState({});
    const [marketCapPercentage, setMarketCapPercentage] = useState({})
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState()
    const url = "https://api.coingecko.com/api/v3/global"
    const setPieData = (data)=>{
        setMarketCapPercentage(data?.data?.market_cap_percentage)
        setGlobalData(data?.data)
    }
    useEffect(() => {


        fetchAPI(url,setPieData,setError,setLoading,"pieChartTimestamp", "pieChartData")
        // const fetchGlobalData = async ()=>{
        //     try{
        //         await axios.get(url).then(response =>{
        //             setGlobalData(response.data?.data?.market_cap_percentage || {})
        //             // console.log(response.data.data.market_cap_percentage)
        //         })
        //     }catch(error){
        //         console.log(error)
        //     }
        //
        //
        // }
        // fetchGlobalData();


    }, []);
    const coinLabels = [...Object.keys(marketCapPercentage), 'Other Coins'];
    const sum = Object.values(marketCapPercentage).reduce((acc, value) => acc + value, 0);
    const coinPercentages = [...Object.values(marketCapPercentage), 100-sum]

    const data = {
        labels: coinLabels,
        datasets: [
            {
                label: 'Market Percentage',
                data: Object.values(coinPercentages),
                backgroundColor: [
                    'rgba(68, 114, 196, 0.5)',
                    'rgba(112, 173, 71, 0.5)',
                    'rgba(192, 80, 77, 0.5)',
                    'rgba(255, 192, 0, 0.5)',
                    'rgba(149, 79, 114, 0.5)',
                    'rgba(79, 129, 189, 0.5)',
                    'rgba(75, 172, 198, 0.5)',
                    'rgba(247, 150, 70, 0.5)',
                    'rgba(255, 255, 0, 0.5)',
                    'rgba(192, 192, 192, 0.5)',
                    'rgba(160, 160, 160, 0.5)',
                ],
                borderColor: [
                    'rgba(68, 114, 196, 1)',
                    'rgba(112, 173, 71, 1)',
                    'rgba(192, 80, 77, 1)',
                    'rgba(255, 192, 0, 1)',
                    'rgba(149, 79, 114, 1)',
                    'rgba(79, 129, 189, 1)',
                    'rgba(75, 172, 198, 1)',
                    'rgba(247, 150, 70, 1)',
                    'rgba(255, 255, 0, 1)',
                    'rgba(192, 192, 192, 1)',
                    'rgba(160, 160, 160, 1)',
                ],
                borderWidth: 1,
            },
        ],
    };
const  options ={
        responsive: true,
            plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                    text: 'Global Market Cap'
            }
        }
    }


    if(error){
        return <div>Something went wrong! {error} Please try again!</div>
    }

    if (loading){
        return <div>Loading...</div>
    }
// console.log(globalData)
    const statsTitles = {
        left_1: "Active Cryptocurrencies",
        left_2: "Ongoing ICOs",
        right_1: "Upcoming ICOs",
        right_2: "Ended ICOs"
    }

    const statsTable ={
        left_1: formatNumber(globalData?.active_cryptocurrencies),
        left_2: formatNumber(globalData?.ongoing_icos),
        right_1: formatNumber(globalData?.upcoming_icos) || 0,
        right_2: formatNumber(globalData?.ended_icos),
    }

    const statsTitles2 = {
        left_1: "Total Market Cap",
        left_2: "Market Cap 24h % change",
        right_1: "Markets",
        right_2: "Total Volume"
    }

    const statsTable2 ={
        left_1: "$"+formatNumber(globalData?.total_market_cap?.usd),
        left_2: formatNumber(globalData?.market_cap_change_percentage_24h_usd)+"%",
        right_1: formatNumber(globalData?.markets),
        right_2: "$"+formatNumber(globalData?.total_volume?.usd),
    }
    return (
        <div >
            <div>
                <h1>Global Market Data</h1>
            </div>
            <StatsTable stats={statsTable} titles={statsTitles}/>
            <StatsTable stats ={statsTable2} titles={statsTitles2}/>
            <div className="content">
                <h1>Global Market Cap Percentage</h1>
            </div>
            <div className="content">
                <Pie className={'chart'} data={data} options={options} />
            </div>

        </div>
    );
};

export default CoinPieChart;