import React from 'react';
import {formatCompactNumber, formatNumber, formatPercentages} from '../utils/utils';
import {Link} from "react-router-dom";

const Template = ({coinData}) => {
    return (
        <table>
            <thead>
            <tr>
                <th>Rank</th>
                <th>Name</th>
                {/*<th></th>*/}
                <th>Price</th>
                <th>Market Cap</th>
                <th className={'hide-mobile'}>Market Cap Rank</th>
                <th className={'hide-mobile'}>High 24h</th>
                <th className={'hide-mobile'}>Low 24h</th>
                <th>24h %</th>
                {/* &#177;Add more columns here as needed */}
            </tr>
            </thead>
            <tbody>
            {coinData.map((coin) => (



                <tr key={coin.id}>
                    <td>{coin.market_cap_rank ? coin.market_cap_rank : "-"}</td>
                    <td className={'centerItems'}>
                        <Link to={`/coin/${coin.id}`}>
                        <div className={'testCol'}>
                        <img className={'table-img'} src={coin.image}
                             alt={coin.name}/>
                        <p>{coin.name}</p>
                        </div></Link>
                    </td>
                    {/*<td>*/}

                    {/*</td>*/}
                    <td>{coin.current_price ? formatNumber(coin.current_price) : '-'}</td>
                    <td>{coin.market_cap ? formatCompactNumber(coin.market_cap) : "-"}</td>
                    <td className={'hide-mobile'}>{coin.market_cap_rank ? formatNumber(coin.market_cap_rank) : "-"}</td>
                    <td className={'hide-mobile'}>{coin?.high_24h ? formatNumber(coin.high_24h) : "-"}</td>
                    <td className={'hide-mobile'}>{coin.low_24h ? formatNumber(coin.low_24h) : "-"}</td>
                    <td>{coin.price_change_percentage_24h ? formatPercentages(coin.price_change_percentage_24h) : "-"}</td>

                </tr>
            ))}
            </tbody>
        </table>
    );
};

export default Template;
