import React, {useEffect, useState} from 'react';
import MultiCarousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import CoinInfo from "../../routes/CoinInfo";
import {Link} from "react-router-dom";
import NftInfo from "../../routes/NftInfo";
import NftCardTemplate from "../CardTemplates/NftCardTemplate";
import {fetchAPI} from "../../utils/utils";
import TrendingCategories from "./TrendingCategories";

const responsive = {
    desktop: {
        breakpoint: {max: 3000, min: 1024},
        items: 3,
        slidesToSlide: 2 // optional, default to 1.
    },
    tablet: {
        breakpoint: {max: 1024, min: 600},
        items: 2,
        slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
        breakpoint: {max: 600, min: 0},
        items: 1,
        slidesToSlide: 1 // optional, default to 1.
    }
};
const TrendingCoinsCarousel = () => {
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState();
    const [trendingCoins, setTrendingCoins] = useState([]);
    const [trendingNFTs, setTrendingNFTs] = useState([])
    const [categories, setCategories] = useState([])
    const url = "https://api.coingecko.com/api/v3/search/trending";
    const setTrendingData = (data)=>{
        setTrendingCoins(data.coins)
        setTrendingNFTs(data.nfts)
        const categoriesWithRank = data.categories.map((category, index) => ({ ...category, rank: index + 1 }));
        setCategories(categoriesWithRank)
    }
    useEffect(() => {
        fetchAPI(url,setTrendingData,setError,setLoading,"trendingTimestamp", "trendingData" )

    }, []);

    const coins = trendingCoins.map((item, i) => {
        return (
            <Link to={`/coin/${item.item.id}`} element={<CoinInfo />} key={item.name + "1"}>
                <NftCardTemplate item={item.item} key={item.item.name} rank={i}/>
            </Link>)

    })

    const nfts = trendingNFTs.map((item, i)=>{
        return (
            <Link to={`/nft/${item.id}`} element={<NftInfo />} key={item?.name}>
                <NftCardTemplate item={item} key={item?.id} rank={i}/>
            </Link>
        )
        // console.log(item)
    })
    if(error){
        return <div>Something went wrong! {error} Please try again!</div>
    }
    if (loading){
        return <div>Loading...</div>
    }
    return (
        <div className={"coin-container"}>
            <div className="content">
                <h1>Trending Coins</h1>
            </div>
            <div className={"content"}>

                <MultiCarousel
                    showDots={false}
                    autoPlay={true}
                    autoPlaySpeed={2000}
                    infinite={true}
                    responsive={responsive}
                    transitionDuration={2000}
                    className={'p-0'}
                    containerClass={"carousel"}
                    // className={"p-2"}
                    itemClass={"carousel-item"}

                    customTransition="all 1s linear"
                    centerMode={false}
                    // draggable
                    arrows={true}
                    focusOnSelect={false}

                >
                    {coins}
                </MultiCarousel>
            </div>
            <div className="content">
                <h1>Trending NFTs</h1>
            </div>
            <div className={"content"}>

                <MultiCarousel
                    showDots={false}
                    autoPlay={true}
                    autoPlaySpeed={2000}
                    infinite={true}
                    responsive={responsive}
                    transitionDuration={2000}
                    className={'p-0'}
                    containerClass={"carousel"}
                    // className={"p-2"}
                    itemClass={"carousel-item"}

                    customTransition="all 1s linear"
                    centerMode={false}
                    // draggable
                    arrows={true}
                    focusOnSelect={false}

                >
                    {nfts}
                </MultiCarousel>
            </div>
            <div className={'content'}>
                <h1>Trending Categories</h1>
            </div>
            <div className="content">
                <TrendingCategories data={categories}/>
            </div>
        </div>

    );
};

export default TrendingCoinsCarousel;