import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import axios from "axios";
import './CoinInfo.css'
import DOMPurify from 'dompurify';
import CoinDetails from "../components/CoinDetails";
import InformationHeader from "../components/InformationTemplates/InformationHeader";
import {calculateDecimalDigits, coinTableHeaders, fetchAPI, formatNumber} from "../utils/utils";
import TimelineTable from "../components/InformationTemplates/TimelineTable";
import StatsTable from "../components/InformationTemplates/StatsTable";
import AboutSection from "../components/InformationTemplates/AboutSection";
import coin from "../components/Coin";

const CoinInfo = () => {
    const [coinInfo, setCoinInfo] = useState({});
    const params = useParams();
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState();

    //
    // const localStorageKeys = Object.keys(localStorage);
    // console.log(localStorageKeys)
    // localStorageKeys.forEach(key => {
    //     if (key.includes("coinId") && key!==(params.coinId+"coinId")) {
    //         localStorage.removeItem(key);
    //         console.log("Remove ",key)
    //     }
    // });
    // // if( localStorage.getItem("coinID"))
    // fetchAPI(url,setCoinInfo,setError,setLoading,"coinIDTimestamp",params.coinId+"coinId" )
    const url = `https://api.coingecko.com/api/v3/coins/${params.coinId}`;
    useEffect(() => {


        fetchAPI(url,setCoinInfo,setError,setLoading, 'coinInfoTimestamp', `coinID${params.coinId}`,'coinID')
    }, [url,params]);

    if (error) {
        return <div>Something went wrong! {error} Please try again!</div>
    }

    if (loading) {
        return <div>Loading...</div>
    }
    const statsTitles = {
        left_1: "24 Hour low",
        left_2: "24 Hour high",
        right_1: "Market Cap",
        right_2: "Circulating Supply"
    }
    const statsTable ={
        left_1: coinInfo?.market_data?.low_24h?.usd ? '$'+formatNumber(coinInfo?.market_data?.low_24h?.usd):"-",
        left_2: coinInfo?.market_data?.high_24h?.usd ? '$'+formatNumber(coinInfo?.market_data?.high_24h?.usd):'-',
        right_1: coinInfo?.market_data?.market_cap?.usd ? "$"+coinInfo?.market_data?.market_cap?.usd?.toLocaleString():"-",
        right_2: coinInfo?.market_data?.circulating_supply ?"$"+coinInfo?.market_data?.circulating_supply?.toLocaleString():'-',
    }

    return (
        <div>
            <div className={'coin-container'}>
                <InformationHeader infoObject={coinInfo}/>
                <TimelineTable infoObject={coinInfo} tableHeaders={coinTableHeaders}/>
                <StatsTable titles={statsTitles} stats={statsTable}/>
                <AboutSection description={coinInfo?.description?.en}/>
                <div className="content">
                    <CoinDetails coinId={params.coinId}/>
                </div>

            </div>

            {/*<CoinLineChart />*/}
        </div>
    );
};

export default CoinInfo;