import axios from "axios";


export function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export const coinTableHeaders = ['1h', '24h', '7d', '14d', '30d', '1y']
export const nftTableHeaders = ['24h', '7d', '14d', '30d', '60d', '1y']

export function formatCompactNumber(number) {
    if (number < 1000) {
        return number?.toFixed(2);
    } else if (number >= 1000 && number < 1_000_000) {
        return (number / 1000)?.toFixed(1) + "K";
    } else if (number >= 1_000_000 && number < 1_000_000_000) {
        return (number / 1_000_000)?.toFixed(1) + "M";
    } else if (number >= 1_000_000_000 && number < 1_000_000_000_000) {
        return (number / 1_000_000_000)?.toFixed(1) + "B";
    } else if (number >= 1_000_000_000_000 && number < 1_000_000_000_000_000) {
        return (number / 1_000_000_000_000)?.toFixed(1) + "T";
    }
}


export function fetchAPI(url, setComponentData, setError, setLoading, timestampName, localVariableName, searchID = "none") {
    const fetchMarkets = async () => {
        setLoading(true)
        // abortControllerRef.current?.abort();
        // abortControllerRef.current = new AbortController();

        try {
            console.log("trying to fetch data")
            // const response = await axios.get(url, {
            //     signal: abortControllerRef.current?.signal,
            // });
            const response = await axios.get(url)
            console.log("Fetched data")
            setComponentData(response.data)
            // const coinResp = await response.json();
            // setCoins(coinResp)
            localStorage.setItem(localVariableName, JSON.stringify(response.data));
            // Update the last fetch timestamp in localStorage
            localStorage.setItem(timestampName, new Date().getTime().toString());
        } catch (e) {
            // if(e.name ==='AbortError'){
            //     console.log("Aborted");
            //     return;
            // }
            console.log("Error from trying to fetch ", localVariableName);
            console.log(e)
            setError(e.message)
        } finally {
            setLoading(false);
        }


        // axios.get(url).then((response) => {
        //     setCoins(response.data)
        //     // console.log(response.data)
        // }).catch((error) => {
        //     console.log(error)
        // })

    }
    // Still there will be an error if i search other categories
    const categoriesIDKey = Object.keys(localStorage).find(key => key.includes(searchID));
    // console.log(categoriesIDKey)
    if (categoriesIDKey && categoriesIDKey !== localVariableName) {
        // Delete the existing key and corresponding timestamp
        localStorage.removeItem(categoriesIDKey);
        localStorage.removeItem(timestampName);
    }
    const lastFetchTimestamp = localStorage.getItem(timestampName);
    const currentTime = new Date().getTime();
    console.log((currentTime - lastFetchTimestamp) / 1000, "seconds have passed for ", localVariableName)
    // Check if enough time has passed since the last fetch
    if (!lastFetchTimestamp || currentTime - lastFetchTimestamp > 30000) {
        // Make the API call
        console.log("calls api")
        fetchMarkets()

        // Update the last fetch timestamp in localStorage
        // localStorage.setItem('lastFetchTimestamp', currentTime.toString());
    } else {
        // Set the coins from localStorage if not enough time has passed
        const storedCoins = localStorage.getItem(localVariableName);
        if (storedCoins) {
            setComponentData(JSON.parse(storedCoins));
        }
    }
}

// Updated the calculation of decimal digits to handle numbers in scientific notation more effectively
function calculateDecimalDigits(number, coindId, col) {
    // Convert the number to its precision representation
    // console.log(number)
    if (number ===undefined){
        return 0
    }
    const precision = Math.abs(number)?.toPrecision();
    // console.log("calculateDecimalDigits")
    // console.log("precision ",precision)
    // Check if the precision representation contains 'e' indicating scientific notation
    const exponentIndex = precision?.indexOf('e');
    // console.log("exponentIndex ",exponentIndex)
    if (exponentIndex !== -1) {
        // Extract the exponent part and convert it to an absolute integer
        // console.log(precision, coindId, col)
        //
        // console.log("there is e inside ",Math.abs(parseInt(precision?.slice(exponentIndex + 1))))
        return Math.abs(parseInt(precision?.slice(exponentIndex + 1)));
    }

    // If not in scientific notation, proceed with the previous logic for non-scientific notation numbers
    const decimalIndex = precision?.indexOf('.');
    if (decimalIndex === -1) {
        return 0;
    }
    // console.log("decimalIndex ",decimalIndex)
    let nonZeroDigitIndex = decimalIndex;

    // Count non-zero digits and stop when 3 non-zero digits are found
    let nonZeroCount = 0;
    // maybe keep it 3
    while (nonZeroDigitIndex < precision.length && nonZeroCount < 2) {
        if (precision[nonZeroDigitIndex] !== '0' && precision[nonZeroDigitIndex] !== '.') {
            nonZeroCount++;
        }
        nonZeroDigitIndex++;
    }
    // console.log("nonZeroCount ",nonZeroCount)
    // console.log("nonZeroDigitIndex ",nonZeroDigitIndex)
    // // If there are no non-zero digits, return 2
    // console.log(decimalIndex,">", 1, decimalIndex > 1 )
    // console.log('returns ',decimalIndex > 1 ? nonZeroCount : nonZeroDigitIndex - 1)
    return decimalIndex > 1 ? nonZeroCount : nonZeroDigitIndex - 1;
}

// Function to remove trailing zero from a number
function removeTrailingZero(number) {
    const numberString = number?.toString();
    if (numberString?.indexOf('.') === -1) {
        return numberString
    }

    if (numberString === '0') {
        return '0';
    }

    // Check if the last character is '0'
    if (numberString?.endsWith('0')) {
        // Remove the trailing '0'
        return numberString.slice(0, -1);
    }

    return number;
}

// Function to format a number with specified minimum and maximum fractional digits
export function formatNumber(number, coindId = "", col = "") {
    const digits = calculateDecimalDigits(number, coindId, col);
    const hasDecimal = digits > 0;
    if(number===undefined){
        return "-"
    }
    // Format the number using toLocaleString with the calculated digits
    const formattedNumber = number?.toLocaleString(undefined, {
        minimumFractionDigits: hasDecimal ? digits : 0,
        maximumFractionDigits: hasDecimal ? digits : 2, // Max 2 decimals for large numbers
    });
    // const temp = removeTrailingZero(formattedNumber)
    // Remove trailing zero after formatting
    // console.log(typeof temp)
    return removeTrailingZero(formattedNumber);
}

export function formatPercentages(number, basedOn="none") {
    let temp;
    // console.log("basedOn ",basedOn)
    // console.log("number ",number)
    if(number===undefined){
        return "No data"
    }
    if(number==='No data'){
        return 'No data'
    }
    if(basedOn !=='none'){
        temp = formatNumber(basedOn);
    }else{
        temp = formatNumber(number)
    }
     const formattedNumber = formatNumber(Math.abs(number))
    // If the number is too big and has a coma "," i must add this
    //parseFloat(formatNumber(number)?.replace(/,/g, ''));

    // console.log(`is negative ${formattedNumber}`,formattedNumber>0, typeof formattedNumber)
    const arrow = temp > 0 ? '' : temp < 0 ? '' : '';
    // console.log(formattedNumber > 0 ? 'green' : formattedNumber < 0 ? 'red' : 'black')
    if(formattedNumber ===undefined){
        return "No data"
    }
    return (
        <span className={'formattedPercentages'}
              style={{color: temp > 0 ? 'lightgreen' : temp < 0 ? 'red' : ''}}>
            <span className={temp > 0 ? 'arrowUp' : 'arrowDown'}>
                {arrow}
            </span>
            {(formattedNumber)}%
        </span>
    )
}



