import {
    Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend,
} from 'chart.js';
import {Line} from 'react-chartjs-2';
import {useEffect, useRef, useState} from "react";
import zoomPlugin from 'chartjs-plugin-zoom';
import {formatCompactNumber} from "../../utils/utils";

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, zoomPlugin,);




const CoinLineChart = ({coindId, coinHistoryData}) => {
    const [prices, setPrices] = useState([])
    const [marketCap, setMarketCap] = useState([])
    const [totalVolumes, setTotalVolumes] = useState([])
    const chartRef = useRef(null);
    useEffect(() => {
        setPrices(coinHistoryData?.prices || [])
        setMarketCap(coinHistoryData?.market_caps || [])
        setTotalVolumes(coinHistoryData?.total_volumes || [])

        // console.log("PRices are", prices)
    }, [coinHistoryData]);

    const labels = prices.map(entry => new Date(entry[0]).toLocaleDateString('en-US'));

    const data = {
        labels, datasets: [{
            label: `Prices`,
            data: prices.map(entry => entry[1]),
            borderColor: 'rgb(255, 99, 132)',
            backgroundColor: 'rgba(255, 99, 132, 0.5)',
        }, {
            label: `Market_Caps`,
            data: marketCap.map(entry => entry[1]),
            borderColor: 'rgb(53, 162, 235)',
            backgroundColor: 'rgba(53, 162, 235, 0.5)',
        }, {
            label: `Total Volumes`,
            data: totalVolumes.map(entry => entry[1]),
            borderColor: 'rgb(53, 235, 53)',
            backgroundColor: 'rgba(53, 235, 53, 0.5)',
        },

        ],
    };

    const options = {
        maintainAspectRatio:false,
        responsive: true,
        scales: {
            y: {
                ticks: {
                    callback: function (value, index, ticks) {
                         return "$" + formatCompactNumber(value)
                    }
                }
            }
        },
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true, text: `${coindId.toUpperCase()}`,
            },
            zoom: {
                zoom: {
                    wheel: {
                        enabled: true,
                    },
                    pinch: {
                        enabled: true
                    },

                    mode: 'xy',
                },

                pan: {
                    enabled: true,
                    mode: 'xy',
                },
            },

        },

    };


    // https://stackoverflow.com/questions/74133676/react-chartjs-2-reset-zoom
    const handleResetZoom = () => {
        if (chartRef && chartRef.current) {
            chartRef.current.resetZoom();
        }
    };

    return (

        <div className={''}>
        <Line className={"chart"} ref={chartRef} data={data} options={options}/>
        <div className={"reset-button-container"}>
            <button className={'reset-button'} onClick={(e) => {
                e.preventDefault()
                handleResetZoom()
            }}>Reset zoom
            </button>
        </div>

    </div>)

}

export default CoinLineChart;
