import React, {useEffect, useState} from 'react';
import {useLocation, useParams} from "react-router-dom";
import axios from "axios";
import Template from "../components/Template";
import StatsTable from "../components/InformationTemplates/StatsTable";
import {fetchAPI, formatNumber, formatPercentages} from "../utils/utils";

const CategoriesInfo = () => {
    let {state} = useLocation();
    const params = useParams()
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState();
    console.log(state)
    console.log(params.categoryId)
    const url = `https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&category=${params.categoryId}&order=market_cap_desc&per_page=100&page=1&sparkline=false&locale=en`
    const [categoriesCoins, setCategoriesCoins] = useState([])
    useEffect(() => {
        fetchAPI(url,setCategoriesCoins,setError,setLoading,"categoriesTimestamp",`categoriesID${params.categoryId}`,"categoriesID")

    }, [url,params.categoryId]);
    const statsTitles1 = {
        hasTitle: false,
        mainTitle: "",
        left_1: "Trending Rank",
        left_2: "Coins count",
        right_1: "Market Cap",
        right_2: "Market Cap 24h % Change"
    }
        //had this on the right_1 ?.toLocaleString()
    const statsTable1 = {
        left_1: state?.coin?.rank,
        left_2: state?.coin?.coins_count,
        right_1: formatNumber(state?.coin?.data?.market_cap)==="No data"? "No data":"$"+formatNumber(state?.coin?.data?.market_cap),
        right_2: formatPercentages(state?.coin?.data?.market_cap_change_percentage_24h?.usd),
    }
    if(error){
        return <div>Something went wrong! {error} Please try again!</div>
    }
    if (loading){
        return <div>Loading...</div>
    }
    return (
        <div>
            <h1 className="content">
                <div className={"content-space"}>
                    {state?.coin.name}
                    <div style={{marginLeft:"2rem"}}>
                        {state?.coin?.data?.sparkline && <img className={'sparkline'} style={{width: "auto", height: "auto"}}
                              src={state?.coin?.data?.sparkline} alt={''}/>}
                    </div>

                </div>


            </h1>

            <StatsTable stats={statsTable1} titles={statsTitles1}/>
            <div className="content">
                <Template coinData={categoriesCoins}/>
            </div>

        </div>
    );
};

export default CategoriesInfo;