import React from 'react';
import {Card} from "react-bootstrap";

const NftCardTemplate = ({item,rank}) => {
    const {name, symbol, large,small,thumb, data, market_cap_rank} = item;
    const thumbImg = thumb.replace("/small/","/large/")

    return (
        <div className={''}>
            <Card>

                <Card.Title>
                    <h3> #{rank + 1} {name}</h3>


                </Card.Title>
                {/*{market_cap_rank ? <h4>Market Cap Rank #{item.market_cap_rank} </h4> : null}*/}

                <Card.Img
                    variant={"top"}
                    src={large || thumbImg}
                    title={name}
                    alt={name || "No data"}
                    className={"card-img"}
                />

                <Card.Text style={{display: "flex", alignItems:"center", padding:"20px"}}>

                    {/*<p style={{marginRight:"1.8rem"}}>{data?.floor_price} {data.price} </p>*/}
                    {/*<img*/}
                    {/*style={{width: "70px", height: "auto", margin: "0.3rem"}}*/}
                    {/*alt={'sparkline'}*/}
                    {/*src={data?.sparkline}/>*/}
                    {data?.floor_price} {data.price}
                    <img
                        style={{width: "70px", height: "auto", marginLeft: "2.5rem"}}
                        alt={'sparkline'}
                        src={data?.sparkline}/>

                </Card.Text>

            </Card>

        </div>
    );
};

export default NftCardTemplate;