import React, {useState} from 'react';
import {CopyToClipboard} from "react-copy-to-clipboard";
import {IoIosCheckmarkCircleOutline, IoIosCopy} from "react-icons/io";

const AdressSection = ({contractAddress}) => {
    const [copied, setCopied] = useState(false)
    return (
        <div className="content">
            <div className="copy-clipboard">
                <p>Contract Adress: <code className={'address'}>{contractAddress}</code></p>
                <CopyToClipboard text={contractAddress}>
                    <button className={'copy-clipboard'} onClick={() => {
                        setCopied(!copied)
                        if (!copied) {
                            alert("Copied!")
                        }
                    }}>
                        Copy {copied ? <IoIosCheckmarkCircleOutline style={{
                        fontSize: "1.2rem"
                    }}/> : <IoIosCopy style={{
                        fontSize: "1.2rem"
                    }}/>}
                    </button>
                </CopyToClipboard>
            </div>
        </div>
    );
};

export default AdressSection;