                    {infoObject.floor_price_30d_percentage_change ?
                        <td>{formatPercentages(infoObject.floor_price_30d_percentage_change?.usd?.toFixed(2))}</td> : null}


                    {infoObject.market_data?.price_change_percentage_30d_in_currency.usd ?
                        <td>{formatPercentages(infoObject.market_data.price_change_percentage_30d_in_currency.usd?.toFixed(2))} </td> : null}
                    {infoObject.floor_price_60d_percentage_change ?
                        <td>{formatPercentages(infoObject.floor_price_60d_percentage_change?.usd?.toFixed(2))}</td> : null}

                    {infoObject.market_data?.price_change_percentage_1y_in_currency?.usd ?
                        <td>{formatPercentages(infoObject.market_data.price_change_percentage_1y_in_currency.usd?.toFixed(2))} </td> : null}
                    {infoObject.floor_price_1y_percentage_change ?
                        <td>{formatPercentages(infoObject.floor_price_1y_percentage_change?.usd.toFixed(2))}</td> : null}


