import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import axios from "axios";
import InformationHeader from "../components/InformationTemplates/InformationHeader";
import StatsTable from "../components/InformationTemplates/StatsTable";
import {formatNumber} from "../utils/utils";

const ExchangesInfo = () => {
    const params = useParams();

    const url= `https://api.coingecko.com/api/v3/exchanges/${params.exchangeId}`;

    const [exchangeData, setExchangeData] = useState({});
    useEffect(() => {
        axios.get(url).then((resp)=>{
            console.log(resp.data)
            setExchangeData(resp.data)
        }).catch((err)=>{
            console.log(err)
        })
    }, [url,params.exchangeId]);

const titles1 ={
    hasTitle:false,
    mainTitle:'',
    left_1:"Year Established",
    left_2:"Country",
    right_1:"Trade Volume 24h BTC",
    right_2:'Normalized'
}

const stats1 = {

    left_1:exchangeData?.year_established,
    left_2:exchangeData?.country,
    right_1: formatNumber(exchangeData?.trade_volume_24h_btc),
    right_2: formatNumber(exchangeData?.trade_volume_24h_btc_normalized),
}

    const titles2 ={
        hasTitle:false,
        mainTitle:'',
        left_1:"Trust Score",
        left_2:"Trust Rank",
        right_1:"Centralized",
        right_2:'Has Trading Incentive'
    }

    const stats2 = {

        left_1:exchangeData?.trust_score,
        left_2:exchangeData?.trust_score_rank,
        right_1: exchangeData?.centralized ?"Yes" :"No",
        right_2: exchangeData?.has_trading_incentive ? "Yes":"No",
    }
    return (
        <div className={''}>
            <div className="content">
                <h1>{exchangeData?.name}<img src={exchangeData.image} alt={''}/></h1>

            </div>
            <StatsTable stats={stats1} titles={titles1}/>
            <StatsTable stats={stats2} titles={titles2}/>
        </div>
    );
};

export default ExchangesInfo;