import React, {useEffect, useState} from 'react';
import {formatCompactNumber, formatPercentages} from "../../utils/utils";
import {Link} from "react-router-dom";

const TrendingCategories = ({data}) => {
    const [sortBy, setSortBy] = useState(null);
    const [sortOrder, setSortOrder] = useState('asc');
    const [activeColumn, setActiveColumn] = useState(0)


    useEffect(() => {
        if (data[0]?.market_cap_1h_change) {
            setActiveColumn(1)
        } else if (data[0]?.data?.market_cap_change_percentage_24h) {
            setActiveColumn(2)
        } else {
            setActiveColumn(3)
        }
    }, [data]);
    const handleSort = (columnName) => {
        if (sortBy === columnName) {

            setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
        } else {

            setSortBy(columnName);
            setSortOrder('asc');
        }
    };
    const getSortIcon = (columnName) => {
        if (sortBy === columnName) {
            return sortOrder === 'asc' ? '' : '';
        }
        return null;
    };
    const sortedData = data.sort((a, b) => {
        let aValue, bValue;

        if (sortBy === 'name') {
            aValue = a[sortBy];
            bValue = b[sortBy];
            return sortOrder === 'asc' ? aValue.localeCompare(bValue) : bValue.localeCompare(aValue);
        } else if (sortBy === 'market_cap') {
            aValue = sortBy ? a['data'][sortBy] : 0;
            bValue = sortBy ? b['data'][sortBy] : 0;
        }else if (sortBy === 'market_cap_btc') {
            aValue = sortBy ? a['data'][sortBy] : 0;
            bValue = sortBy ? b['data'][sortBy] : 0;
        } else if (sortBy === 'market_cap_change_percentage_24h') {
            aValue = sortBy ? a['data'][sortBy]?.usd : 0;
            bValue = sortBy ? b['data'][sortBy]?.usd : 0;
        } else if (sortBy === 'total_volume') {
            aValue = sortBy ? a['data'][sortBy] : 0;
            bValue = sortBy ? b['data'][sortBy] : 0;
        } else {
            aValue = sortBy ? a[sortBy] : 0;
            bValue = sortBy ? b[sortBy] : 0;
        }


        if (sortOrder === 'asc') {

            return aValue - bValue;
        } else {

            return bValue - aValue;
        }
    });
    console.log(activeColumn)
    return (
        <table>
            <thead>
            <tr>
                <th className={'hide-mobile'} onClick={() => handleSort('rank')}>{getSortIcon('rank')} Rank</th>
                <th onClick={() => handleSort('name')}>
                    {getSortIcon('name')} Name
                </th>

                <th className={'hide-mobile'}
                    onClick={() => handleSort('coins_count')}>{getSortIcon('coins_count')} Coins Count
                </th>
                {activeColumn===1 && <th
                    onClick={() => handleSort('market_cap_1h_change')}>{getSortIcon('market_cap_1h_change')} Market Cap 1h%
                </th>}
                {activeColumn===2 && <th
                    onClick={() => handleSort('market_cap_change_percentage_24h')}>{getSortIcon('market_cap_change_percentage_24h')} Market
                    Cap 24h%
                </th>}
                {activeColumn===3 && <th
                    onClick={() => handleSort('market_cap_btc')}>{getSortIcon('market_cap_btc')} Market Cap BTC
                </th>}
                <th onClick={() => handleSort('market_cap')}>{getSortIcon('market_cap')}Market Cap</th>
                <th onClick={() => handleSort('total_volume')}>{getSortIcon('total_volume')}Total Volume</th>
                <th className={'hide-mobile'}>Sparkline</th>
            </tr>
            </thead>
            <tbody>
            {sortedData.map((coin, i) => (
                <tr key={coin.id}>
                    <td className={'hide-mobile'}>{coin?.rank}</td>
                    <td>
                        <Link
                            to={`/categories/${coin.slug}`}
                            state={{coin}}
                        > <span className={'spanTry'}>
                            {coin.name}
                            </span>
                        </Link>
                        {/*Added the span to try and fit names and numbers in tables to be responsive
                        Check index.css and CoinInfo.css
                    */}
                    </td>
                    <td className={'hide-mobile'}>{coin.coins_count}</td>
                    {activeColumn===1 && <td>{formatPercentages(coin?.market_cap_1h_change)}</td>}
                    {activeColumn===2 && <td>{formatPercentages(coin?.data?.market_cap_change_percentage_24h?.usd)}</td>}
                    {activeColumn===3 && <td>{formatCompactNumber(coin?.data?.market_cap_btc)}</td>}
                    <td>{formatCompactNumber(coin.data.market_cap)}</td>
                    <td>{formatCompactNumber(coin.data.total_volume)}</td>
                    <td className={'hide-mobile'}>
                        <img src={coin.data.sparkline} alt="Sparkline"/>
                    </td>
                </tr>
            ))}
            </tbody>
        </table>
    );
};

export default TrendingCategories;
