import {useEffect, useState} from "react";
import axios from "axios";
import CoinsList from "./components/CoinsList";
import NavBar from "./components/NavBar";
import {Routes, Route} from "react-router-dom";
import CoinInfo from "./routes/CoinInfo";
import NftInfo from "./routes/NftInfo";
import GlobalMarketData from "./components/GlobalMarketData";
import Home from "./components/Home";
import CategoriesInfo from "./routes/CategoriesInfo";
import NavBars from "./components/NavBar";
import Missing from "./Missing";
import SearchResult from "./routes/SearchResult";
import ExchangesInfo from "./routes/ExchangesInfo";



function App() {

  return (
    <div className={'coin-container'}>
        <NavBars/>
        <Routes>
            <Route path='' element={<Home/>}/>
            <Route path={'/coin'} element={<CoinInfo />}>
                <Route path={':coinId'} element={<CoinInfo />}/>
            </Route>
            <Route path={'/globalMarketData'} element={<GlobalMarketData/>}/>
            <Route path={'/exchangesList'} element={<ExchangesInfo/>}/>
            <Route path={'/nft'} element={<NftInfo />}>
                <Route path={':nftId'} element={<NftInfo />}/>
            </Route>
            <Route path={'/categories'} element={<CategoriesInfo />}>
                <Route path={':categoryId'} element={<CategoriesInfo />}/>
            </Route>
            <Route path={'/exchanges'} element={<ExchangesInfo />}>
                <Route path={':exchangeId'} element={<ExchangesInfo />}/>
            </Route>
            <Route path={'/searchResult'} element={<SearchResult/>}/>


            <Route path="*" element={<Missing />} />
        </Routes>
        {/*<CoinsList coins={coins}/>*/}
    </div>
  );
}

export default App;
