import {useEffect, useRef, useState} from "react";
import axios from "axios";
import CoinsList from "./CoinsList";
import coin from "./Coin";
import useMarketData from "../hooks/useMarketData";
import {fetchAPI} from "../utils/utils";
import CoinPieChart from "./charts/CoinPieChart";
import TrendingCoinsCarousel from "./Trending/TrendingCoinsCarousel";

const Home = () => {
    // const abortControllerRef = useRef(null); //now its on the utils function
    const {coins, setCoins} = useMarketData(); //have to delete this
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState();
    const [show,setShow] = useState(false)
    const url = "https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=10&page=1&sparkline=false&locale=en"


    useEffect(() => {

        fetchAPI(url, setCoins, setError, setLoading, "globalMarketTimestamp","globalMarketData" )


    }, [setCoins]);


    if(error){
        return <div>Something went wrong! {error} Please try again!</div>
    }

    if (loading){
        return <div>Loading...</div>
    }
    return (
        <div>
            <TrendingCoinsCarousel/>
            <CoinsList coins={coins}/>
            {/*<button onClick={()=>{setShow(!show)}}>To print Global market pie chart</button>*/}
            {/*{show && <CoinPieChart/>}*/}
        </div>

    )


}

export default Home;